.footer {
    display: flex;
    height: 150px;
    background-color: black;
}

.footer-columns {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.footer-column {
    display: flex;
    flex-direction: column;
}

.footer-column-links {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.footer-column-title {
    font-family: "Philosopher", sans-serif;
    color: white;
}

.footer-column-link {
    cursor: pointer;
    font-family: "Philosopher", sans-serif;
    color:rgb(161, 161, 161);
    margin: 0;
}