body {
  margin: 0;
  padding: 0;
}

/* Ширина и цвет полосы прокрутки */
::-webkit-scrollbar {
  width: 10px; /* Ширина полосы прокрутки */
}

::-webkit-scrollbar-track {
  background: #ffffff; /* Цвет фона дорожки */
}

::-webkit-scrollbar-thumb {
  background: #000000; /* Цвет полосы прокрутки */
}
