.genre {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    padding-top: 20px;
}

.genre-title {
    font-family: "Philosopher", sans-serif;
    margin: 0 auto;
    margin-bottom: 20px;
}

.genre-cards {
    gap: 50px;
    margin: 0 auto;
    display: flex;
    width: 80%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content:space-between;
}

.genre-card {
    display: flex;
    flex-direction: column;
    height: 450px;
    width: 350px;
    border-radius: 5px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4);
    transition: all 0.3s ease;
}

.genre-card:hover {
    transform: scale(1.05);
}

.genre-card img {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    width: 100%;
    height: 85%;
}

.genre-card-title {
    margin: 0 auto;
    h2 {
        font-family: "Philosopher", sans-serif;
        cursor: pointer;
    }
}