.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000; /* Позволяет хедеру быть поверх других элементов */
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    width: 100%;
    background-color: black;
}

body {
    margin-top: 50px;
}

.header-links {
    display: flex;
    gap: 20px;
    margin-left: 20px;
}

.header-link {
    text-decoration: none;
    color: white;
    font-size: 20px;
}

.header-link:hover {
    color:rgb(175, 170, 170);
}

.search-box {
    position: relative;
    width: 250px;
    margin-right: 20px;
}

.search-box input[type="text"] {
    width: 100%;
    padding: 8px 40px 8px 10px;
    border: 2px solid rgb(255, 255, 255);
    border-radius: 10px;
    box-sizing: border-box;
    transition: all 0.3s ease; /* Добавлено для плавного перехода */
}

.search-box input[type="text"]:focus {
    outline: none; /* Убирает стандартные рамки при фокусе */
    border-color: rgb(255, 255, 255); /* Меняет цвет рамки при фокусе */
    transform: scale(1.05); /* Увеличивает поле при фокусе */
}

.search-icon {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    fill: black;
}

