.scroll-top-btn {
  position: fixed;
  bottom: -50px;
  right: 20px;
  background-color: black;
  color: white;
  padding: 10px 20px;
  border: 1px solid white;
  border-radius: 5px;
  cursor: pointer;
  transition: bottom 0.3s, background 0.3s;
  z-index: 1000;
}

.scroll-top-btn.show {
  bottom: 20px;
}

.scroll-top-btn:hover {
  background-color: rgb(61, 60, 60);
}
