.classic {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 20px;
}

.classic h1 {
    margin: 0 auto;
    margin-top:20px;
    margin-bottom: 20px;
}

.classic-books {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-direction: row;
    gap:70px;
}

.book {
    display: flex;
    flex-direction: column;
    width:200px;
    height: 600px;
    justify-content:space-between;
}

.book img {
    width: 200px;
    height: 309px;
}

.rating {
    margin: 0 auto;
}

.rating span {
    cursor: pointer;
    font-size: 24px; /* Увеличение размера звезд */
    margin: 0 4px; /* Добавлено небольшое расстояние между звездами */
}

.book-btns {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    gap:10px;
}

.book-btn {
    background-color: black;
    border:0px;
    padding: 10px;
    color:white;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
    &:hover {
        background-color: rgb(61, 60, 60);
    }
}

.book-rating-and-btns {
    display: flex;
    flex-direction:column;
    gap:10px;
}

.book-price b {
    font-size: 25px;
}

.book-name b {
    font-size: 25px;
}

.book-author-price {
    height: 80%;
}

.book-author {
    font-size:20px;
}

